@font-face {
    font-family: 'Pretendard-Regular';
    src: url('https://fastly.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff') format('woff');
    font-weight: 400;
    font-style: normal;
}

body {
    overflow-y: scroll; /* y축만 스크롤 가능 */
    overflow-x: hidden; /* x축 스크롤 숨기기 */
    scrollbar-width: none; /* Firefox에서 스크롤바 숨기기 */
    background-color: #1E1E1E;
    font-family: 'Pretendard-Regular';
    width: 100vw;
    height: 100vh;
    position: relative;
  }

.navbar {
    float: left;
    height: 50px;
    width: 100vw;
    margin: 0;
    padding: 0;
    background-color: #3C3C3C;
    color : #CCCCCC;
    justify-content: center;
    align-items: center;
  }
  
  .first_navbar {
    float: left;
    position: absolute;
    top: 60px;
    background-color: #3C3C3C;
    width: 7%;
    padding: 0; /* padding을 0으로 설정하여 여백 없애기 */
    height: 100vh;
    display: flex;
    justify-content: center; /* 수평 중앙 정렬 */
    align-items: center; /* 세로 중앙 정렬 */
}

.first_navbar ul {
    padding: 0; /* ul의 기본 padding 제거 */
    margin: 0;  /* ul의 기본 margin 제거 */
    display: flex;
    flex-direction: column; /* 세로 방향으로 정렬 */
    margin-top: 30%;
    /* justify-content: center; */
    align-items: center;
    height: 100%;
}

.first_navbar ul li {
    display: flex;
    list-style: none;
    justify-content: center; /* 수평 중앙 정렬 */
    align-items: center; /* 세로 중앙 정렬 */
    height: auto; /* 부모 요소의 높이에 맞게 세로 정렬 */
    padding: 0; /* 패딩 제거 */
    margin-bottom: 40%;
}

.first_navbar ul li img {
    width: 70%; /* 이미지 크기 조정 */
    padding: 0; /* 수직 패딩 제거 */
    margin: 0; /* 여백 제거 */
}

.navbar ul {
    list-style: none;
    display: flex;
    gap: 1.2rem;
  }
  
.navbar a {
    text-decoration: none;
    font-weight: bold;
  }

.second_navbar {
    float: left;
    position: absolute;
    top: 60px;
    left: 7.5%;
    background-color: #252526;
    width: 11%;
    padding: 0; /* padding을 0으로 설정하여 여백 없애기 */
    height: 100vh;
    display: flex;
    justify-content: center; /* 수평 중앙 정렬 */
    align-items: center; /* 세로 중앙 정렬 */
}

.second_navbar ul {
    padding: 0; /* ul의 기본 padding 제거 */
    margin: 0;  /* ul의 기본 margin 제거 */
    flex-direction: column; /* 세로 방향으로 정렬 */
    margin-top: 30%;
    /* justify-content: center; */
    align-items: center;
    height: 100%;
}

.second_navbar ul li {
  display: flex; /* 이미지와 텍스트를 나란히 정렬 */
  align-items: center; /* 세로 중앙 정렬 */
  list-style: none;
  height: 0%; /* 필요에 따라 높이 조정 */
  padding: 0;
  margin-bottom: 40%;
  text-align: left;
  font-size: 0.9rem;
  color: #0477BF;
  font-weight: bolder;
}

.second_navbar ul li img {
  width: 14px; /* 이미지 크기 */
  margin-right: 2px;
}

.second_navbar ul li {
  text-decoration: none; /* 밑줄 제거 */
  color: #CCCCCC; /* 글자 색상 */
}

.third_navbar {
  position: absolute;
  top: 11%;
  left: 17.5%;
  float: left;
  height: 30px;
  width: 100vw;
  margin: 0;
  padding: 0;
  background-color: #252526;
  color : #CCCCCC;
  justify-content: center;
  align-items: center;
}

.third_navbar ul {
  padding: 0; /* ul의 기본 padding 제거 */
  margin: 0;  /* ul의 기본 margin 제거 */
  display: flex; /* 플렉스 컨테이너로 설정 */
  flex-direction: row;
  align-items: center; /* 왼쪽 정렬 */
  height: 100%;
  margin-left: 2%;
}

.third_navbar ul li {
  list-style: none; /* 불릿 제거 */
  display: flex; /* 플렉스 아이템으로 설정 */
  justify-content: flex-start; /* 왼쪽 정렬 */
  align-items: center; /* 세로 중앙 정렬 */
  height: auto; /* 내용에 맞게 높이 설정 */
  padding: 0; /* 패딩 제거 */
  margin-right: 2%; /* 각 아이템 사이 간격 */
  text-align: left; /* 텍스트 정렬 */
  font-size: 0.7rem; /* 글자 크기 */
  color: #0477BF; /* 텍스트 색상 */
  font-weight: bolder; /* 텍스트 두께 */
}

.third_navbar ul li a {
  text-decoration: none; /* 밑줄 제거 */
  color: #CCCCCC; /* 글자 색상 */
  display: flex; /* 플렉스 적용 */
  align-items: center; /* 텍스트와 이미지 중앙 정렬 */
}

.third_navbar ul li img {
  width: 14px; /* 이미지 크기 */
  margin-right: 4px; /* 이미지와 텍스트 사이 간격 */
  vertical-align: middle; /* 텍스트와 이미지 수평 맞춤 */
}

.Footer {
  width: 100vw;
  height: 6%; /* 고정 높이 설정 */
  background-color: #0477BF;
  position: fixed;
  bottom: 0;
  display: flex; /* 플렉스 컨테이너 */
  flex-direction: row; /* 가로 방향 배치 */
  justify-content: space-between; /* 양쪽 정렬 */
  align-items: center; /* 세로 중앙 정렬 */
  color: #CCCCCC;
  padding: 0 1rem; /* 좌우 여백 추가 */
  font-size: 0.8rem;
}

.Footer ul {
  list-style: none;
  display: flex; /* 리스트 항목 가로 배치 */
  flex-direction: row;
  padding: 0;
  margin: 0;
}

.Footer ul li {
  width: 40%;
  display: flex; /* 이미지와 텍스트 배치를 위한 플렉스 */
  align-items: center;
  margin-right: 3px;
}

.Footer ul li img {
  width: 25%; /* 이미지 크기 균일화 */
  height: auto; /* 비율 유지 */
  margin-right: 0.2rem; /* 텍스트와 이미지 간 간격 */
}

.Footer_First {
  display: flex;
  flex-direction: row;
}

.Footer_Second {
  display: flex;
  flex-direction: row;
}

.Footer_Second ul li {
  margin-left: 20px;
}

.third_navbar ul li.active {
  background-color: #f2d78890; /* 활성화된 li의 배경색 */
  padding: 0.2rem 0;
}

.third_navbar ul li.active a {
  text-decoration: none; /* 필요하면 밑줄 제거 */
  color: #1E1E1E; /* 텍스트 색상 */
}

.home {
  display: grid;
  place-items: center; /* 가로, 세로 중앙 정렬 */
  height: calc(100vh - 50px); /* 화면 높이에서 navbar 높이 제외 */
  padding: 0; /* 내부 여백 제거 */
  width: 100%; /* 화면 너비에 맞춤 */
  overflow-y: scroll; /* y축만 스크롤 가능 */
  overflow-x: hidden; /* x축 스크롤 숨기기 */
  margin-left: 5%;
}

p {
  color: #CCCCCC;
  line-height: 150%;
}

h1 {
  color: #CCCCCC;
  margin-bottom: 5%;
}

.Me {
  width: 20%;
  margin-top: 5%;
  border-radius: 25%;
}

.about {
  display: grid;
  place-items: center; /* 가로, 세로 중앙 정렬 */
  height: calc(100vh - 50px); /* 화면 높이에서 navbar 높이 제외 */
  padding: 0; /* 내부 여백 제거 */
  width: 100%; /* 화면 너비에 맞춤 */
  overflow: hidden; /* 내용이 넘치지 않도록 설정 */
  margin-left: 5%;
  color: #CCCCCC;
}

.about h2 {
  margin-top: 10%;
}

.portfolio {
  display: grid;
  place-items: center; /* 가로, 세로 중앙 정렬 */
  height: calc(100vh - 50px); /* 화면 높이에서 navbar 높이 제외 */
  padding: 0; /* 내부 여백 제거 */
  width: 100%; /* 화면 너비에 맞춤 */
  overflow-y: scroll; /* Y축 스크롤 허용 */
  margin-left: 10%;
  color: #CCCCCC;
  text-decoration: none;
}

.portfolio h1 {
  margin-top: 10%;
  margin-bottom: 20px;
  color: #CCCCCC;
}

.portfolio a {
  text-decoration: none; /* 링크의 밑줄 없애기 */
}

.portfolio h1,
.portfolio h2,
.portfolio h3,
.portfolio p {
  text-decoration: none; /* 제목과 본문의 밑줄 없애기 */
}

.portfolio-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr); /* 3열로 구성 */
  gap: 20px;
  padding: 20px;
  width: 70%;
  height: 100vh;
}

.portfolio-item {
  padding: 20px;
  border-radius: 8px;
  text-align: center;
  border: 1px #cccccc solid;
  transition: transform 0.3s ease-in-out;
}

.portfolio-item:hover {
  transform: scale(1.05); /* 호버 시 확대 효과 */
}

.portfolio-item img {
  width: 100%;
  height: auto;
  border-radius: 8px;
}

.portfolio-item h3 {
  margin-top: 10px;
  font-size: 1.2rem;
  color: #CCCCCC;
}

.portfolio-item p {
  font-size: 1rem;
  color: #CCCCCC;
}

.contact {
  display: grid;
  place-items: center; /* 가로, 세로 중앙 정렬 */
  height: calc(100vh - 50px); /* 화면 높이에서 navbar 높이 제외 */
  padding: 0; /* 내부 여백 제거 */
  width: 100%; /* 화면 너비에 맞춤 */
  overflow: hidden; /* 내용이 넘치지 않도록 설정 */
  margin-left: 5%;
}

.contact p {
  color: #CCCCCC;
  font-weight: bold;
  line-height: 200%;
}

/* 동그란 텍스트 컨테이너 */
.circle-container {
  position: relative;
  width: 300px;
  height: 300px;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* 중앙 프로필 이미지 */
.profile-img {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  object-fit: cover;
}

/* 원형 텍스트 컨테이너 */
.circle-text {
  position: absolute;
  width: 100%;
  height: 100%;
  font-size: 14px;
  font-weight: bold;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
}

/* 원을 따라 배치될 텍스트 */
.circle-text span {
  position: absolute;
  top: 10%;
  left: 50%;
  transform-origin: 0 150px; /* 회전 중심 설정 */
  white-space: nowrap;
}

@keyframes rotateText {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.circle-text {
  animation: rotateText 10s linear infinite;
}